import store from "../store"

const fetchDataRequest = () => {
    return {
        type: "CHECK_DATA_REQUEST",
    };
};

const fetchDataSuccess = (payload) => {
    return {
        type: "CHECK_DATA_SUCCESS",
        payload: payload,
    };
};

const fetchDataFailed = (payload) => {
    return {
        type: "CHECK_DATA_FAILED",
        payload: payload,
    };
};

export const fetchData = (account) => {
    return async (dispatch) => {
        dispatch(fetchDataRequest());
        try{
            let allOwnerBunnys = await store.getState().blockchain.bunnyToken.methods.getOwnerBunnys(account).call();
            let allBunnys = await store.getState().blockchain.bunnyToken.methods.getBunnys().call();
            dispatch(fetchDataSuccess({allBunnys, allOwnerBunnys}));
        } catch(err) {
            console.log(err);
            dispatch(fetchDataFailed("¡No se ha podido cargar los datos del Smart Contract!"))
        }
    };
};