import React from "react";
import {parts} from "../parts/parts";


const BunnyRenderer = ({bunny = null, size = 200, style}) => {
    
    let dnaStr = String(bunny.dna);
    while(dnaStr.length < 16) dnaStr="0" + dnaStr;

    let bunnyDetails = {
        bg: dnaStr.substring(0,2) % 5,
    
        line: dnaStr.substring(2,4) % 5,
        addon: dnaStr.substring(4,6) % 5,
        
        name: bunny.name,
    }
    
    const bunnyStyle = {
        width: "100%",
        height: "100%",
        position: "absolute",
    }

    return (
        <div 
        style = {{
            minWidth : size,
            minHeight : size, 
            background : "blue",
            position: "relative",
            ...style,
        }} 
        >
            
            <img alt ={"bg"} src = {parts.bg[bunnyDetails.bg]}
            style = {bunnyStyle} />

            

            <img alt ={"line"} src = {parts.line[bunnyDetails.line]}
            style = {bunnyStyle} />

            <img alt ={"addon"} src = {parts.addon[bunnyDetails.addon]}
            style = {bunnyStyle} />

            

           
        </div>
    );

};

export default BunnyRenderer

