import Web3 from "web3"
import BunnyToken from "../../contracts/BunnyToken.json"
import { fetchData } from "../data/dataActions"

const connectRequest = () => {
    return {
        type: "CONNECTION_REQUEST",
    };
};

const connectSuccess = (payload) => {
    return {
        type: "CONNECTION_SUCCESS",
        payload: payload,
    };
};

const connectFailed = (payload) => {
    return {
        type: "CONNECTION_FAILED",
        payload: payload,
    };
};

const updateAccountRequest = (payload) => {
    return {
        type: "UPDATE_ACCOUNT",
        payload: payload,
    };
};

export const connect = () => {
    return async (dispatch) => {
        dispatch(connectRequest());
        if(window.ethereum) {
            let web3 = new Web3(window.ethereum);
            try {
                const accounts = await window.ethereum.request({method: "eth_requestAccounts",});// cambiamos "eth_accounts" por "eth_requestAccounts"
                console.log(accounts);
                const networkId = await window.ethereum.request({method: "net_version", });
                console.log(networkId);
                // ID de la Blockchain de despliegue
                // Ganache -> 5777
                // BSC Testnet -> 97
                // MATIC/Polygon -> 80001
                // Rinkeby -> 4
                if (networkId == 4) {
                    const networkData = BunnyToken.networks[networkId];
                    const abi = BunnyToken.abi;
                    const address = networkData.address;
                    const bunnyToken = new web3.eth.Contract(abi, address);
                    console.log(bunnyToken);
                    dispatch(
                        connectSuccess({
                            account: accounts[0],
                            bunnyToken: bunnyToken,
                            web3: web3,
                        })
                    );

                    window.ethereum.on("accountsChanged", (accounts) => {
                        dispatch(updateAccount(accounts[0]));
                    });

                    window.ethereum.on("chainChanged", () => {
                        window.location.reload();
                    });
                } else {
                    dispatch(connectFailed("¡Cambia a la red adecuada!"));
                    } 
                } catch(err) {
                    dispatch(connectFailed("¡Conecta tu billetera!"))
                }
            } else {
                dispatch(connectFailed("¡Instala Metamask!"))
            }
        };
    };

    export const updateAccount = (account) => {
        return async (dispatch) => {
            dispatch(updateAccountRequest({account: account}));
            dispatch(fetchData(account));
        };
    };
